@font-face {
  font-family: "Clear Sans";
  font-style: normal;
  font-weight: 100;
  src: local("ClearSans-Thin"), local("Clear Sans Thin"),
  url("ClearSans-Thin.eot"),
  url("ClearSans-Thin.eot?#iefix") format("embedded-opentype"),
  url("ClearSans-Thin.woff") format("woff"),
  url("ClearSans-Thin.ttf") format("truetype"),
  url("ClearSans-Thin.svg") format("svg");
}

